.grid.desktop{
	grid-template-columns: 1fr 1fr 1fr 1fr;
	// background-image: url('../img/pdp/huge.jpg');
	background-attachment: fixed;
	background-size: 100% auto;
	background-position: 0 90%;
	margin-top: 2rem;
	
	&.thirds{
		grid-template-columns: 1fr 1fr 1fr;
		.cell{
			a{
				margin: 2rem 0 0;
				display: block;
				text-align: center;
			}
			&.padded{
				padding: 2rem;
			}
		}
	}
	&.nomargin{
		margin: 0;
	}
	&.mixnmatch{
		&:before{
			top: 50%;
			width: 96px;
			height: 96px;
			background-image: url('../img/pdp/mix.jpg');
		}
	}
	.cell{
		box-shadow: 0 0 0 1px #e5e5e5;
		&.double{
			height: 50vw;
		}
		&.single{
			height: 25vw;
		}
		&.empty{
			background-color: transparent;
		}
		&.padded-double{
			padding: 2rem;
		}
		&.tall{
			grid-row: span 1.5
		}
		&.product{
			cursor: pointer;
			.title{
				opacity: 0;
				transform: translateY(0.5rem);
				@include transition(all, $longDuration);
			}
			img{
				transform-origin: center center;
				transform: scale(1);
				@include transition(transform, $longDuration);
			}
			&:hover{
				.title{
					transform: translateY(0);
					opacity: 1;
				}
				img{
					transform: scale(1.08);
				}
			}
			&.num{
				&:before{
					position: absolute;
					top: 1rem;
					left: 1rem;
					width: 1.5rem;
					height: 1.5rem;
					border: 1px solid black;
					content: "";
					z-index: 99;
					transform: rotate(.125001turn);
					transform-origin: center center;
				}
				&:after{
					position: absolute;
					top: 1rem;
					left: 1rem;
					width: 1.5rem;
					height: 1.5rem;
					content: "A";
					z-index: 99;
					text-align: center;
					line-height: 1.5rem;
					font-size: .875rem;
				}
				&.num1{
					&:after{content: '1'}
				}
				&.num2{
					&:after{content: '2'}
				}
				&.num3{
					&:after{content: '3'}
				}
			}
		}
		&.social{
			background-color: $white;
			position: relative;
			img{
				padding: 1.5rem 2rem 2rem;
			}
			.socialInfo{
				width: 100%;
				position: absolute;
				bottom: 0;
				left: 0;
				padding: 0 2rem 0.75rem;
				height: 2rem;
				align-items: flex-end;
				img{
					padding: 0;
				}
			}
		}
		&.texture{
			background-image: url(../img/pdp/linearBkg.png);
			background-size: cover;
		}
		&.halftexture{
			background-image: url(../img/global/halfTexture.jpg);
			background-size: auto 100%;
			background-position: bottom right;
			background-repeat: no-repeat;
		}

		&.stroke{
			position: relative;
			&:before{
				width: 200%;
				height: 1px;
				background-color: #e5e5e5;
				position: absolute;
				top: 0.5px;
				left: 0.5px;
				transform-origin: top left;
				transform: rotate(.125turn);
				content: ""
			}
		}
		&.noBorder{
			box-shadow: 0 0 0 1px #fff
		}
		h3{
				font-size: 2.25rem;
			}
		&.doubleWidth{
			grid-column: span 2;
			height: 25vw;
			display: flex;
			padding: 0;
			.content{
				padding: 2rem;
			}
			.left, .right{
				flex-grow: 1;
				padding: 2rem;
				width: 50%;
			}
			
			p{
				margin: 2rem 0 1rem;
				font-size: 1.125rem;
				line-height: 1.25
			}
			.right{
				img{
					width: 100%;
				}
			}
		}
		&.reverseStroke{
			transform: rotate(.25turn);
		}
	}
	&.pdp{
		h2{
			margin: 0;
		}
		.content{
			margin: 0;
			padding: 8rem 6rem;
		}
	}
	&.hp{
		.socialInfo{
			padding-left: 0;
		}
	}
	&.plp {
		grid-auto-flow: dense;
		grid-auto-rows: 12.5vw;
		@include transition(all);
		.cell{
			background-color: white;
			height: 100%;
			.socialInfo{
				padding-left: 0;
			}
			&.header{
				position: relative;
				.btn{
					position: absolute;
					bottom: 2rem;
				}
			}
			&.half{
				grid-row: span 1;
			}
			&.single{
				grid-row: span 2;
			}
			&.tall{
				grid-row: span 3;
			}
			&.double{
				grid-row: span 4;
			}
			&.halfEmpty{
				background-image: url(../img/global/halfEmpty.jpg);
				background-size: cover;
			}
		}
		&.hidden{
			opacity: 0;
			height: 0;
			transform: translatey(-2rem)
		}
	}
}
