@function sqrt($r) {
  $x0: 1;
  $x1: $x0;
 
  @for $i from 1 through 10 {
    $x1: $x0 - ($x0 * $x0 - abs($r)) / (2 * $x0);
    $x0: $x1;
  }
 
  @return $x1;
}



.trendModule{
	width: 100vw;
	height: 50vw;
	position: relative;
	background-size: cover;

	// background-attachment: fixed;
	overflow: hidden;
	border-bottom: 1px solid #e5e5e5;
	&:before, &:after{
		position: absolute;
		top: 0;
		left: 0;
		width: sqrt((50 * 50) / 2) * 1vw;
		height: sqrt((50 * 50) / 2) * 1vw;
		content: '';
		background: repeating-linear-gradient(
		    0deg,
		    white,
		    white 5px,
		    #e5e5e5 6px,
		    #e5e5e5 7px
		  );
	}
	&:before{
		transform-origin: top left;
   		transform: rotate(45deg) translate(100%, 0%);
	}
	&:after{
		transform-origin: top left;
   		transform: rotate(45deg) translate(100%, -200%);
	}
	.maskLeft{
		width: 50%;
		height: 100%;
		float: left;
		shape-outside: polygon(100% 0, 0 0, 0 50%, 0 100%, 100% 100%, 0 50%);
	}
	.maskRight{
		width: 50%;
		height: 100%;
		float: right;
		shape-outside: polygon(100% 0, 0 0, 100% 50%, 0 100%, 100% 100%, 100% 50%);
	}
	.copy{
		width: 50vw;
		height: 50vw;
		margin-left: 50%;
		transform: translate(-50%, 0);
		background-color: white;
		clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
		position: relative;
		
		.content{
			padding-top: 16.5rem;
			p{
				font-size: 1.125rem;
				margin-bottom: 1rem;
			}
			h1{
				margin-bottom: 1rem;
				font-size: 3.75rem;
			}
			a{
				margin-top: 1rem;
			}
			width: 100%;
			height: 100%;
			text-align: center;
		}
	}
}
