				width: 50vw;
			}
			&.full{
				background-position: top left;
				background-size: 100%;
			}
			&.unbordered{
				border-color: transparent;
			}
		}
		&.active{
			pointer-events: initial;
			transform: translateY(0);
			opacity: 1;
		}
	}
}

